import React from 'react';

import CenterBox from '../components/CenterBox';
import H1 from '../components/H1';
import Page from '../components/Page';

export default () => (
  <Page>
    <CenterBox>
      <H1>
        We are having too much traffic. Please come back in a few minutes.
      </H1>
    </CenterBox>
  </Page>
);



